<template>
	<div>
		<div id="mapcointainer" ref="map"></div>
	</div>
</template>
<script type="text/javascript">
	import {getHttp} from '../js/HttpServer.js'
	let thisa = null;
	export default{
		data(){
			thisa = this;
			return {
				 tabPosition: 'bottom'

			}
		},
		mounted() {
				thisa.$refs.map.style.height=(window.innerHeight-131)/16+'em';
	      thisa.map = new AMap.Map('mapcointainer', {
	        resizeEnable: true,
	        zoom:12,

	        }
	      );
	      AMap.plugin(['AMap.ToolBar','AMap.Scale','AMap.OverView'],function(){
	        var toolBar = new AMap.ToolBar();
	        var scale = new AMap.Scale();
	        var OverView = new AMap.OverView({isOpen:true})
	        thisa.map.addControl(toolBar);
	        thisa.map.addControl(scale);
	        thisa.map.addControl(OverView);
   			})
	      thisa.map.plugin('AMap.Geolocation', function() {
	        let geolocation = new AMap.Geolocation({
	          enableHighAccuracy: true,
	          timeout: 10000,
	          buttonOffset: new AMap.Pixel(20, 10),
	          zoomToAccuracy: false,
	          buttonPosition:'RB'
	        });
	        thisa.map.addControl(geolocation);
	        function onComplete(data) {
	          // alert('定位成功');
	          // alert('经度:'+data.position.getLng()+",纬度:"+data.position.getLat());
	          thisa.getMates(data.position,thisa.map);
	        }
	        function onError(data) {
	          alert('定位失败');
	        }
	        AMap.event.addListener(geolocation, 'complete', onComplete);//返回定位信息
	        AMap.event.addListener(geolocation, 'error', onError);      //返回定位出错信息
	    		geolocation.getCurrentPosition();
	    	//---------------添加点击事件---------------------------------
	  //   	function eventHandler(e){ 
			//   var lnglat = e.lnglat;
			//   alert('精度:'+lnglat.lng+',维度:'+lnglat.lat);
			// }
			// var listener = AMap.event.addListener(thisa.map,"click",eventHandler);
	      })
	  },
	  methods:{
		  	 getMates:function(position,map){
		  	 	let url = '/restful/activity/getMates?lng='+position.getLng()+'&lat='+position.getLat();
		  	 	getHttp(url,function(res){
		  	 		console.log(res.data);
		  	 		let arr = res.data;
		  	 		let len = arr.length;
		  	 		for(let i=0;i<len;i++){
		  	 			let marker = new AMap.Marker({                 
						  map:map,                 
						  position:new AMap.LngLat(arr[i].lng, arr[i].lat), 
						  icon:"http://webapi.amap.com/images/0.png",
						  offset: new AMap.Pixel(-18, -21),
						  content:arr[i].atitle,
					    })

					    // marker.setLabel({  
			      //           offset: new AMap.Pixel(-18, -21),  
			      //           content: 'hello'
			      //       });
			            // marker.label.setStyle({color:red});
			      		// for(let key in marker){
			      		// 	console.log(key);
			      		// }
		  	 		}
		      	    
      			});
		  	 }
		  }
	}
</script>


