<template>
	<div>
		<el-row class="container" id="foot">
		  <el-col :xs="1" :sm="1">&nbsp;</el-col>
		  <el-col :xs="22" :sm="22">
		  	 <foot ref="foot"/>
		  </el-col>
		  <el-col :xs="1" :sm="1">&nbsp;</el-col>
		</el-row>

		<el-row class="container" id="top">
		  <el-col :xs="1" :sm="1">&nbsp;</el-col>
		  <el-col :xs="22" :sm="22">
		  	<table width="100%">
          <tr valign="bottom">
            <td style="width:64px;">
              <img src="../assets/logo_1.jpg" alt="" style="width:32px;">约杯
            </td>
            <td>
              <div style="margin-top:20px;">
                <el-input placeholder="请输入内容" class="input-with-select"></el-select>
                <el-button slot="append" icon="el-icon-search"></el-button>
                </el-input>
              </div>
            </td>
          </tr>
        </table>
		  </el-col>
		  <el-col :xs="1" :sm="1">&nbsp;</el-col>
		</el-row>

		<el-row class="container" id="content">
		  <el-col :xs="1" :sm="1">&nbsp;</el-col>
		  <el-col :xs="22" :sm="22">
		  	<MapGaode ref="MapGaode"/>
		  </el-col>
		  <el-col :xs="1" :sm="2">&nbsp;</el-col>
		</el-row>

		
	</div>
</template>

<script type="text/javascript">
	import MapGaode from './GaoDe.vue'
	import foot from './foot.vue'
	let thisa = null;
	export default{
		data(){
			thisa = this;
			return {
			}
		},
		components:{
    	MapGaode:MapGaode,
    	foot:foot
  	},
	}
</script>
<style>
  .el-col {
    border-radius: 4px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .bg-purple {
    background: #d3dce6;
  }
  .bg-purple-light {
    background: #e5e9f2;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
</style>


